export default {
  data() {
    return {
      value: '',
      options: [{
        value: '选项1',
        label: '黄金糕'
      }, {
        value: '选项2',
        label: '双皮奶'
      }, {
        value: '选项3',
        label: '蚵仔煎'
      }, {
        value: '选项4',
        label: '龙须面'
      }, {
        value: '选项5',
        label: '北京烤鸭'
      }],
      tableData: [],
      endTime:'',
      startTime:'',
      page: [],
      roleList:[],
      roleId:'',
      currentPage: 1,
      pageSize:10
    }
  },

  mounted() {
  	this.initRole();
  	this.init();

  },

  methods: {
  	init(){
			let temp = this;
			this.$post('/web/queryLog', {
				"startTime":temp.value!=''?temp.value[0]:'',
				"endTime":temp.value!=''?temp.value[1]:'',
				"roleId":temp.roleId,
				"page": temp.currentPage-1,
				"size": temp.pageSize
				}).then((m)=> {
				if(m.resultCode == 0){
					temp.page = m.content.page;
					console.log(temp.page);
				}
			})
		},
		initRole(){
			let temp = this;
			this.$post('/web/queryRole', {
				}).then((m)=> {
				if(m.resultCode == 0){
					temp.roleList = m.content.list;
					temp.roleList.unshift({id: '',name:'请选择'});
				}
			})
			
		},
    handleSizeChange(val) {
      this.pageSize = val;
  		this.init();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
  		this.init();
    }
    
   
  }
}



